import React, { Component } from 'react';
import { Redirect } from 'react-router-dom';

class Home extends Component {
    componentDidMount(){
        this.props.resetRedirect();
    }
    // componentWillUpdate(nextProps, nextState){
    //     localStorage.setItem("phone_num", this.props.phone_num);
    //     alert( "" + localStorage.getItem("phone_num"));
    // }
    render(){
        if(this.props.fireRedirect === true){
            return <Redirect to = {this.props.redirect}/>
        }
        return (
            <div className = "home-content">
                <h5></h5>
                {!this.props.plateMatchesPhoneNum ? 
                    <h5 className = "banner"><br/></h5>
                    : ''}
                {!this.props.phoneExists ? 
                    <h5 className = "banner"><br/></h5>
                    : ''}
                <form onSubmit = {this.props.submitForm}>
                    <input type = "text" name = "phone_num" value = {this.props.phone_num} placeholder = "" onChange = {this.props.handleInputChange}/>
                    <input type = "text" name = "plate" value = {this.props.plate} placeholder = "" onChange = {this.props.handleInputChange}/>
                    <button type = "submit"></button>
                </form>
            </div>
        )
    }
}

export default Home;


