import React, { Component } from 'react';

class CreateRecord extends Component {
    constructor() {
        super();
        var today = new Date();
        var month = ((today.getMonth() + 1) < 10) ? '0' + (today.getMonth() + 1) : (today.getMonth() + 1);
        var date = ((today.getDate() + 1) < 10) ? '0' + today.getDate() : today.getDate();
        this.state = {
            record_time: today.getFullYear() + "-" + month + "-" + date,
            record_name: '',
            record_milage: '', 
            record_operator: '', 
            record_gift: '', 
            record_detail: '', 
            record_id: '',
        }
    }
    componentWillUnmount(){
        this.setState({
            record_time: '',
            record_name: '',
            record_milage: '', 
            record_operator: '', 
            record_gift: '', 
            record_detail: '', 
            record_id: '',
        })
    }
    handleInputChange = (e) => {
        const name = e.target.name;
        const value = e.target.value;
        this.setState({
            [name]: value,
        });
    }
    submitAndClearState(e, record_time, record_name, record_milage, record_operator, record_gift, record_detail, record_id){
        this.props.handleNewRecordSubmit(e, record_time, record_name, record_milage, record_operator, record_gift, record_detail, record_id);
        if(record_time && record_name && record_milage && record_operator && record_gift && record_detail && record_id){
            var today = new Date();
            var month = ((today.getMonth() + 1) < 10) ? '0' + (today.getMonth() + 1) : (today.getMonth() + 1);
            var date = ((today.getDate() + 1) < 10) ? '0' + today.getDate() : today.getDate();
            this.setState({
                record_time: today.getFullYear() + "-" + month + "-" + date,
                record_name: '',
                record_milage: '', 
                record_operator: '', 
                record_gift: '', 
                record_detail: '', 
                record_id: '',
            })
        }
    }
    render(){
        var today = new Date();
        var month = ((today.getMonth() + 1) < 10) ? '0' + (today.getMonth() + 1) : (today.getMonth() + 1);
        var date = ((today.getDate() + 1) < 10) ? '0' + today.getDate() : today.getDate();
        return (
            <form className = "create-record-form" onSubmit = {(e) => this.submitAndClearState(e, this.state.record_time, 
                                                                                                    this.state.record_name, 
                                                                                                    this.state.record_milage, 
                                                                                                    this.state.record_operator, 
                                                                                                    this.state.record_gift, 
                                                                                                    this.state.record_detail, 
                                                                                                    this.props.service_num)}>
                <div className = "create-record-group">
                    <input className = "create-record-input" type = "date" name = "record_time" placeholder = "" onChange = {this.handleInputChange} value = {this.state.record_time}/>
                    <select className = "create-record-input" name = "record_name" onChange = {this.handleInputChange} value = {this.state.record_name}>
                        <option value = "">--</option>
                        <option value = "">****</option>
                        {this.props.service_name_list.map(service => {
                            if (service.type === '')
                            return <option key = {service.id}>{service.record_name}</option>;
                            else
                            return "";
                        })}
                        <option value = "">****</option>
                        {this.props.service_name_list.map(service => {
                            if (service.type === '')
                            return <option key = {service.id}>{service.record_name}</option>;
                            else
                            return "";
                        })}
                        <option value = "">****</option>
                        {this.props.service_name_list.map(service => {
                            if (service.type === '')
                            return <option key = {service.id}>{service.record_name}</option>;
                            else
                            return "";
                        })}
                        <option value = "">****</option>
                        {this.props.service_name_list.map(service => {
                            if (service.type === '')
                            return <option key = {service.id}>{service.record_name}</option>;
                            else
                            return "";
                        })}
                    </select>
                    <input className = "create-record-input" type = 'text' name = 'record_milage' placeholder = "" onChange = {this.handleInputChange} value = {this.state.record_milage}/>
                    <select className = "create-record-input" name = "record_operator" onChange = {this.handleInputChange} value = {this.state.record_operator}>
                        <option value = "">--</option>
                        {this.props.operator_list.map(operator => {
                            if (operator.location === '')
                            return <option key = {operator.id}>{operator.record_operator}</option>;
                            else
                            return "";
                        })}
                        <option value = "">****</option>
                        {this.props.operator_list.map(operator => {
                            if (operator.location === '')
                            return <option key = {operator.id}>{operator.record_operator}</option>;
                            else
                            return "";
                        })}
                        <option value = "">****</option>
                        {this.props.operator_list.map(operator => {
                            if (operator.location === '')
                            return <option key = {operator.id}>{operator.record_operator}</option>;
                            else
                            return "";
                        })}
                        <option value = "">****</option>
                        {this.props.operator_list.map(operator => {
                            if (operator.location === '')
                            return <option key = {operator.id}>{operator.record_operator}</option>;
                            else
                            return "";
                        })}
                    </select>
                    <select className = "create-record-input" name = "record_gift" onChange = {this.handleInputChange} value = {this.state.record_gift}>
                        <option value = "">--</option>
                        {this.props.gift_list.map(gift => {
                            return <option key = {gift.id}>{gift.record_gift}</option>
                        })}
                    </select>
                    <input className = "create-record-input" type = "text" name = "record_detail" placeholder = "" onChange = {this.handleInputChange} value = {this.state.record_detail}/>
                    <div className = "create-record-input"><button className = "form-btn" type = "submit"></button></div>
                </div>
                <div className = "create-record-btn-group">
                    <button id = "delete-btn" onClick = {this.props.switchInputNew}/>
                </div>
            </form>
        )
    }
}

export default CreateRecord;


